/* AFTER-HEADER */

#afterheader{
    /* margin:0; */
    background-color: #edf3fb;
    height: 150px;
    color: black;
}

.afterheader-row{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 2% 10%;
}
/* .afterheader-container .row .box{
    width: 25%;
} */
.afterheader-li{
    list-style: none;
}

.afterheader-li:first-of-type{
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
    font-size: 13px;
    font-weight: 300;
    color: rgb(116, 118, 121);
}

.afterheader-li:nth-of-type(2){
    font-family: 'Abril Fatface', serif;
    padding: 2% 0;
    font-size: 22px;
}

.paragraph{
    color: black;
    font-size: 14px;
}


@media only screen and (max-width:540px){
    /* #afterheader{
        height: 100px;
    } */
    
    .afterheader-container .row .box{
        width: 100%;
    }
    .afterheader-li:nth-of-type(2){
        font-size: 20px;
    }
}

@media only screen and (max-width:375px){
    #afterheader{
        height: 100px;
    }
    .afterheader-li:first-of-type{
        font-size: 12px;
    }
    
    .afterheader-container .row .box{
        width: 100%;
    }
    .afterheader-li:nth-of-type(2){
        font-size: 18px;
    }
}
