/*INTERNACIONAL*/

#international-section{
    height: 850px;
}
.container-int h2{
    margin-top: 5%; 
    margin-left: 10%;
}

.container-int .row:first-of-type{
    margin-top: 5%;
    margin-left: 10%;
}
.container-int .row:first-of-type a{
    color: grey;
}
.container-int .row:first-of-type a:hover {
    color: rgb(151, 126, 83);
}

.container-int p{
    color: black;
    line-height: 1.8em;
    font-size: 13px;
    margin: 5% 10%;
    width: 60%;
}

#internacional{
    height: auto;
    width: 100%;
    margin: 2% 11%;
    background-color: white;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
}

#intl {
    margin: 0 -0.5%;
    height: 250px;
}

#intl .row{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}

#intl h2{
    color: white;
    font-weight: 300px;
    margin-left: 5%;
    position: absolute;
    z-index: 10;
    margin-top: -180px;
}

#intl ion-icon{
    margin-left: 230px;
    margin-top: -175px;
    color: black;
    background-color: white;
    font-size: 19px;
    padding: 3px;
    border-radius: 20px;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
    opacity: 0;
}

#intl:hover ion-icon{
    -webkit-transform: translate( 10px, 0);
            transform: translate( 10px, 0); 
    opacity: 1;
}

.intl-pic-box{
    height: 250px;
    width: 37%;
    margin: 10px;
}

.intl-pic-box img{
    width: 311px;
    height: 250px;
    border: 1px solid rgb(196, 193, 193);
    border-radius: 4px;
    overflow: hidden;
    -webkit-transform: translate .3s;
            transform: translate .3s;
    -webkit-transition: opacity 0.5s, -webkit-transform 0.5s;
    transition: opacity 0.5s, -webkit-transform 0.5s;
    transition: transform 0.5s, opacity 0.5s;
    transition: transform 0.5s, opacity 0.5s, -webkit-transform 0.5s;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}

.intl-pic-box img:hover{
    cursor: pointer;
}

.intl-show-text{
    color: black;
    background-color: rgb(245, 236, 221);
    font-family: 'Roboto', sans-serif;
    margin-top: -5%;
    margin-left: 19%;
    width: 90%;
    text-align: center;
}

#international-section .container-back{
    margin-top: 8%;
}

/* International Pages: Moz, Pt, Sa */
#moz{
    height: auto;
}

#moz .row{
    padding: 5%;
    margin-left: 10%;
}
#moz a{
    color: grey;
}
#moz a:hover{
    color: black;
}

.container-back{
    margin-top: 10%;
    margin-left: 10%;
    -webkit-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 15%;
}

.container-back a,
.container-back a i{
    color: grey;
}

.container-back:hover a,
.container-back:hover a i{
    color: rgb(151, 126, 83);
}


@media only screen and (max-width: 768px){
    #international-section{
        height: 1700px;
    }

    #internacional{
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-flow: column;
                flex-flow: column;
    }
    #intl{
        -webkit-box-flex: 1;
            -ms-flex: 1 100%;
                flex: 1 100%;
        width: 80%;
        height: 300px;
        margin: 0 10%;
    }

    #intl ion-icon{
        margin-left: 200px;
        margin-top: -175px;
        font-size: 19px;
        padding: 3px;
        border-radius: 20px;
    }
}