/*MOBILE NAV BUTTON*/

#mobile-menu{
    top: 0%; 
    left: 85%;
    background-color: white;
    position: fixed;
    z-index: 201!important;
    border-radius: 1px;
    width: 50px;
    height: 50px;
    border:none;
    cursor: pointer;
    margin: 50px auto;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
}

#mobile-menu .bar{
    display: block;
    position: absolute;
    width: 35px;
    height: 2px;
    top: 20px;
    background: rgb(164, 131, 74);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
}

/*MOB NAV BUTTON STRIPES*/
#mobile-menu .bar:first-of-type{
    top: 15px;
}
#mobile-menu .bar:nth-of-type(2){
    top: 24px
}

#mobile-menu .bar:nth-of-type(3){
    top: 33px;
}

#mobile-menu:hover{
    background-color: rgb(164, 131, 74);
}
#mobile-menu:focus{
    outline: none;
}
#mobile-menu:hover .bar{
    background: white;
}

#mobile-menu .open .bar:nth-child(1) {
    top: 23px;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
  }
  
#mobile-menu .open .bar:nth-child(2) {
    opacity: 0;
    left: -60px;
  }
  
#mobile-menu .open .bar:nth-child(3) {
    top: 23px;
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
  }


  @media only screen and (max-width: 1920px){
    #mobile-menu{
        display: none;
    }
  }

  @media only screen and (max-width: 768px){
      #mobile-menu{
        display: block;
        left: 85%;
      }
  }

  @media only screen and (max-width:480px){
    #mobile-menu{
        top:-1%;
        left: 75%;
    }
} 

  @media only screen and (max-width: 414px){
    #mobile-menu{
        top:-2%;
        left: 78%;
    }
  }

  @media only screen and (max-width: 375px){
    #mobile-menu{
        top:-3%;
        left: 76%;
    }
  }

  @media only screen and (max-width: 325px){
    #mobile-menu{
        top:-6%;
        left: 68%;
    }
  }
    @media only screen and (max-width: 320px){
        #mobile-menu{
            top:-6%;
            left: 74%;
        }
  }