/*MOBILE NAV*/

.mobile-nav{
    width: 50%;
    height: 100%;
    top: 0;
    padding-top: 5%;
    position: fixed;
    background: black;
    text-transform: uppercase;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out; 
    z-index: 200 !important;
}
.mobile-nav.slide{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.mobile-nav li{
    padding: 5%;
    transition: all 0.2s ease;
    border-bottom: 1px solid grey;
}

.mobile-nav li a{
    color: white;
}

.mobile-nav li:first-of-type{
    border-top: 1px solid grey;
}

.mobile-nav li:hover{
    padding-left: 10%;
    background-color: white;
}

.mobile-nav li:hover a{
    color:  rgb(151, 126, 83);
}



/*MOBILE NAV BUTTON*/

#mobile-menu{
    top: 0%; 
    left: 85%;
    background-color: white;
    position: fixed;
    z-index: 201!important;
    border-radius: 1px;
    width: 50px;
    height: 50px;
    border:none;
    cursor: pointer;
    margin: 50px auto;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: .5s ease-in-out;
}

#mobile-menu .bar{
    display: block;
    position: absolute;
    width: 35px;
    height: 2px;
    top: 20px;
    background: rgb(164, 131, 74);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: .25s ease-in-out;
}

/*MOB NAV BUTTON STRIPES*/
#mobile-menu .bar:first-of-type{
    top: 15px;
}
#mobile-menu .bar:nth-of-type(2){
    top: 24px
}

#mobile-menu .bar:nth-of-type(3){
    top: 33px;
}

#mobile-menu:hover{
    background-color: rgb(164, 131, 74);
}
#mobile-menu:focus{
    outline: none;
}
#mobile-menu:hover .bar{
    background: white;
}

#mobile-menu .open .bar:nth-child(1) {
    top: 23px;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
  }
  
#mobile-menu .open .bar:nth-child(2) {
    opacity: 0;
    left: -60px;
  }
  
#mobile-menu .open .bar:nth-child(3) {
    top: 23px;
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
  }


  @media only screen and (max-width: 1920px){
