}

@media only screen and (max-width: 480px){
    #atividade h2{
        font-size: 18px;
        margin-top: 15%;
    }
    #atividade p{
        width: 100%;
        font-size: 13px;
    }
    #atividade ul li{
        width: 100%;
        margin-left: 0;
        font-size: 13px;
    }
}
