/* AREA PAGES */

#atividade{
    height: auto;
    margin-left: 15%;
    margin-right: 8%;
    margin-top: 10%;
    margin-bottom: 10%;
}

#atividade .row .location{
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    float: left;
    font-weight: 300;
    margin-top: 2%;
    margin-bottom: 4%;
    padding-bottom: 5%;
    margin-left: 75%;
}

#atividade h2{
    padding: 4% 0; 
}

#atividade p{
    color: black;
    line-height: 1.8em;
    font-size: 14px;
    width: 60%;
}

#atividade ul li{
    font-size: 13px;
    margin-left: 5%;
    width: 60%;
    line-height: 1.8em;
}

#atividade a{
    color: grey;
    -webkit-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
}

#atividade a:hover{
    color: rgb(151, 126, 83);
}

.container-back{
    margin-top: 15%;
    -webkit-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 15%;
}

.container-back:hover i a{
    color: rgb(151, 126, 83);
}

@media only screen and (max-width: 768px){
    #atividade p,
    #atividade ul li{
        width: 90%;
    }
}

@media only screen and (max-width: 480px){
    #atividade h2{
        font-size: 18px;
        margin-top: 15%;
    }
    #atividade p{
        width: 100%;
        font-size: 13px;
    }
    #atividade ul li{
        width: 100%;
        margin-left: 0;
        font-size: 13px;
    }
}