/* ABOUT */

#quemsomos .row:first-of-type{
    margin-top: 5%;
    margin-left: 10%;
}
#quemsomos .row:first-of-type a{
    color: grey;
}
#quemsomos.row:first-of-type a:hover {
    color: rgb(151, 126, 83);
}

#quemsomos a{
    color: rgb(151, 126, 83)
}
#quemsomos a:hover{
    color: black;
}

.container-back{
    margin-top: 15%;
    margin-left: 5%;
    -webkit-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 15%;
}

.container-back a,
.container-back a i{
    color: grey;
}

.container-back:hover a,
.container-back:hover a i{
    color: rgb(151, 126, 83);
}


.lista{
    /* margin-top: 5%; */
    margin: 0 12%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
}

.lista li{
    list-style: none;
    color: white;
    background-color: rgb(151, 126, 83);
    padding: 10px 10px;
    margin: 10px 5px;
    border: 1px solid rgb(151, 126, 83);
    border-radius: 20px;
    text-align: center;
}

.lista li:hover{
    background-color: white;
    color: grey;
}