/* AREAS */

#areas{
    width: 100%;
    height: 1280px;
}

#areas .container .row{
    margin-left: 15%;
    margin-top: 10%;
    color: grey;
    font-family: 'Roboto', sans-serif;
}

#areas .container .row a,
#areas .container .row i{
    color: grey; 
    -webkit-transition: color .2s; 
    transition: color .2s;
}

#areas .container .row a:hover,
#areas .container .row i:hover{
    color: rgb(151, 126, 83);
}

#areas .container p{
    color: black;
    margin-left: 15%;
    margin-top: 5%;
    line-height: 1.8em;
    font-size: 14px;
    width: 60%;
}

/* Picture Box Section*/

.container-areas{
    margin-top: 7%;
    margin-left: 7%;
    margin-right: 7%;
    margin-bottom: 5%;
}

#areabox{
    margin: 0 4px;
}
.box-image{
    margin: 2% 0;
    width: 100%;
    height: 240px;
    border: none;
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    background-size: cover;
    text-rendering: optimizeLegibility;
    overflow-x: hidden;
    overflow-y: hidden;
    color: black;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.box-image:hover{
    -webkit-box-shadow: 5px 5px 10px rgb(149, 150, 160);
            box-shadow: 5px 5px 10px rgb(149, 150, 160);
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}

.box-image img{
    width: 101%;
    height: 270px;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    z-index: 7;
}

#areabox .row{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row;
            flex-flow: row;
}

#areabox h4{
    margin-top: -200px;
    color: white;
    z-index: 10;
    margin-left: 4%;
    font-size: 17px;
}

#areabox ion-icon{
    margin-top: -200px;
    background-color: white;
    border-radius: 20px;
    margin-left: 3%;
    font-size: 20px;
    font-weight: 700;
    color: black;
    float: right;
    padding: 3px 5px;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
    opacity: 0
}

#areabox:hover ion-icon{
    -webkit-transform: translate(20px, 0);
            transform: translate(20px, 0);
    opacity: 1;
}

#areabox p{
    margin-top: -12%;
    margin-left: 1.5%;
    position: absolute;
    color: white;
    z-index: 10;
    display: none;
}

#areabox:hover p{
    display: block;
}

.backarrow-div{
    margin-top: 750px;
    position: absolute;
    width: 100%;
}

.container-back{
    margin-top: 15%;
    margin-left: 15%;
    -webkit-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 15%;
}

.container-back a,
.container-back a i{
    color: grey;
}

.container-back:hover a,
.container-back:hover a i{
    color: rgb(151, 126, 83);
}


@media only screen and (max-width: 854px){
    .box-image{
        height: 206px;
    }
    .box-image img {
        width: 100%;
        height: 206px;
    }
    #areabox h4{
        padding: 20px 0;
        font-size: 15px;
    }

    #areabox ion-icon{
        font-size: 16px;
        margin-top: -180px;
    }
}
@media only screen and (max-width: 768px){
    #areas{
        width: 100%;
        height: 1500px;
    }
    #areas .container-back{
        margin-top: 167%;
    }
    .span-1-of-3 {
        width: 42.26%;
    }

    #areabox {
        margin: -2px 7px;
    }

    .backarrow-div{
        margin-top: -40px;
    }
}
@media only screen and (max-width: 720px){

    .backarrow-div{
        margin-top: 0px;
    }
}
@media only screen and (max-width: 640px){
    .backarrow-div{
        margin-top: 120px;
    }
}

@media only screen and (max-width: 540px){
    #areas{
        height: 2550px;
    }
    .span-1-of-3 {
        width: 100%;
    }
    .box-image {
        width: 73%;
        height: 200px;
    }

    .backarrow-div{
        margin-top: 1320px;
    }
}

@media only screen and (max-width: 480px){
    .backarrow-div{
        margin-top: 1380px;
    }
}

@media only screen and (max-width: 320px){
    .backarrow-div{
        margin-top: 1560px;
    }
}