/* HEADER */

#header{
    height: 700px;
    background-position: fixed;
    background-attachment: fixed;
    overflow-x: hidden;
    margin: 0;
}

#header h1{
    color: white;
    font-weight: 400;
    font-size: 50px;
    margin-left: 45%;
    margin-top: 15%;
}

#header h2{
    color: white;
    font-size: 35px;
    font-family: 'Lato' sans-serif;
    font-style: italic;
    font-weight: 200;
    margin-left: 45%;
    margin-top: -1%;
}

.container-share.row{
    width: 100%;
    font-size: 17px;
}

.container-share .row span{
    margin-top: 15%;
    float: right;
    margin-right: 10%;
    padding: 1%;
}

.container-share .row i{
 color: black;
 font-size: 17px;
}



@media only screen and (max-width: 540px){
    #header h1{
        font-size: 40px; 
        margin-left: 35%;
    }
    #header h2{
        font-size: 27px;
        margin-left: 35%;
        font-weight: 300;
    }
}

@media only screen and (max-width: 540px){
    #header h1{
        font-size: 35px;
    }
    #header h2{
        font-size: 25px;
    }
}

@media only screen and (max-width: 320px){
    #header{
        height: 550px;
    }
    #header h1{
        font-size: 30px;
        margin-top: 27%;
    }
}