/* FOOTER */
footer {
    margin: 0 auto;
    /* height: auto; */
    background-color: #29323e;
    padding: 4%;
}

/* CONTAINER WITH LINKS */
.container-footer{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
}


.container-footer h4{
  font-family: 'Assistant', sans-serif;
  font-weight: 200;
  color: white;
  font-size: 11px;
  text-transform: uppercase;
  margin-left: 8.5%;
}

.container-footer a {
  color: white;
  font-family: 'Assistant', sans-serif;
  font-size: 12px;
  font-weight: 200px;
  -webkit-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out;
  text-decoration: none;
}

.container-footer a:hover{ 
  color: rgb(151, 126, 83);
}

.container-footer .box{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}


.container-footer .box li {
  padding: 1%;
  color: white;
  font-size: 13px;
  font-weight: 200;
  list-style: none;
  margin-left:  8.5%;
  -webkit-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out;
}

#oam{
  width: 180px;
  margin-right: 25%;
}


/* COPYRIGHT */
 
.goodbye-row {
  color: white;
  background-color: rgb(44, 42, 42);
  text-align: center;
  padding: 0.5%;
  width: 100%;
  font-size: 12px;
  font-weight: 200;
  font-family: 'Roboto', sans-serif;
}

.goodbye-row p{
  color: white;
}

.goodbye-row a{
  -webkit-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out;
  color: white;
}

.goodbye-row a:hover{
  color: rgb(151, 126, 83);
}

/* WEATHER + COPYRIGHT + TERMS + SOCIAMEDIA ETC */

.row.weather{
  background: black;
  color: white;
  font-weight: 200;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
}
.row.weather li:first-of-type{
  list-style: none;
  padding: 0.8% 3%; 
  font-size: 13px;
}

.row.weather li:nth-of-type(2),
.row.weather li:nth-of-type(3){
  list-style: none;
  font-size: 11px;
  padding: 0.8% 1%;
}

.row.weather li a{
  -webkit-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out;
  color: white;
}

.row.weather li:hover a{
   color: rgb(151, 126, 83);
}
.row.weather #utc{
  color: rgb(167, 164, 164);
}

.social-media{
  margin-right: 10%;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  padding: 0.8% 1%;
}
.social-media li{
  list-style: none;
}

.social-media li i{
  font-size: 17px;
  padding-left: 25%;
  -webkit-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out;
}

.social-media li:hover i{
  color:  rgb(151, 126, 83);
}


