/* MAIN NAV */

#main-nav {
    color: white;
    /* background-color: rgba(0,0,0,0.45);     */
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 100%;
    padding-bottom: 2%;
    z-index: 200;
  }

  #main-nav .official-logo {
      width: 260px;
      height: auto;
      margin-top: 15%;
      float: left;
      margin-left: 15%;
    }

  #main-nav #navigation-links{
      list-style: none;
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
      -webkit-box-pack: end;
          -ms-flex-pack: end;
              justify-content: flex-end;
      text-transform: uppercase;
      -webkit-box-align: baseline;
          -ms-flex-align: baseline;
              align-items: baseline;
      margin-top: 7.5%; 
      margin-left: 5%;
      margin-right: 10%;
      width: 70%;
    }

  #main-nav .nav-links {
      font-family: "Assistant", sans-serif;
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
      -webkit-box-pack: end;
          -ms-flex-pack: end;
              justify-content: flex-end;
      padding: 7px;
      margin-top: 4%;
      color: white;
      font-weight: 300;
      -webkit-transition: color .2s ease-in-out,padding-top .2s ease-in-out,padding-bottom .2s ease-in-out;
      transition: color .2s ease-in-out,padding-top .2s ease-in-out,padding-bottom .2s ease-in-out;
    }
    
    #navigation-links .nav-links:hover {
      color: rgb(151, 126, 83);
      text-decoration: none;
      cursor: pointer;
    }

/*Scrolled*/

.scrolled{
  position: fixed;
  background-color: black;
  opacity: 0.92;
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  z-index: 200;
}

.official-logo .scrolled{
  width: 150px;
  margin: 1%;
}

  
@media only screen and (max-width: 1920px){
  #mobile-menu{
    display:none;
  }
}

@media only screen and (max-width: 1376px){
  #main-nav .official-logo {
    width: 230px;
    margin-top: 20%;
    margin-left: 15%;
  }
  #main-nav #navigation-links{
    margin-left: 15%;
    margin-right: 5%;
  }
  #main-nav #navigation-links .nav-links {
    font-size: 14px;
  }
}

@media only screen and (max-width: 1215px){
    #main-nav .official-logo {
      width: 200px;
      margin-top: 20%;
      margin-left: 15%;
    }


  }


  @media only screen and (max-width: 1024px){
    #main-nav{
      padding-bottom: 3%;
    }
    #main-nav .official-logo {
      width: 160px;
      margin-top: 20%;
      margin-left: 15%;
    }
    #main-nav #navigation-links .nav-links {
      font-size: 12px;
      margin-top: -10%;
    }
  }

  @media only screen and (max-width: 768px){
    #main-nav #navigation-links{
      display:none;
    }
  }

  @media only screen and (max-width: 600px){

  }


  @media only screen and (max-width: 400px){
    #main-nav{
      padding-bottom: 5%;
    }
    #main-nav .official-logo {
      width: 150px;
    }
  }

  @media only screen and (max-width: 375px){
    #main-nav .official-logo {
      width: 100px;
    }
  }