/* INICIO */

#intro{
    height: auto;
}

#intro .container-inicio .logo-gold{
    width: 180px;
    margin: 5% 10%;
    opacity: 0.6;
}

#intro .container-inicio p{
    color: black;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    width: 55%;
    font-size: 15px;
    font-style: italic;
    margin-top: -10%;
    margin-left: 27%;
    width: 60%;

}
#intro .container{
    margin: 0 15%;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
#intro .intro-box{
    margin: 15% 0.5%;
    border: 1px solid rgb(196, 193, 193);
    border-radius: 3px;
    height: 250px;
    width: 37%;
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1;
    overflow: hidden;
}

#intro .intro-box img{
    width: 100%;
    height: 250px;
    overflow: hidden;
    -webkit-transform: translate .3s;
            transform: translate .3s;
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    -webkit-transition: opacity 0.5s, -webkit-transform 0.5s;
    transition: opacity 0.5s, -webkit-transform 0.5s;
    transition: transform 0.5s, opacity 0.5s;
    transition: transform 0.5s, opacity 0.5s, -webkit-transform 0.5s;
}

#intro .intro-box img:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
}

#intro .intro-box h4{
    font-family: 'Lato', sans-serif;
    position: absolute;
    font-size: 20px;
    font-weight: 200;
    margin-top: -150px;
    margin-left: 2%;
    color: white;
    text-align: center;
}

#intro .intro-box ion-icon{
    color: white;
    margin-top: -150px;
    font-size: 28px;
    float: right;
    margin-right: 15%;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
    opacity: 0;
}

#intro .intro-box:hover ion-icon{
    -webkit-transform: translate( 20px, 0);
            transform: translate( 20px, 0);
    opacity: 1;
}


/* Blockquotes */

#quote{
    margin-top: 15%;
}

.bqt-bg{
    height: 350px;
    background: rgb(245, 236, 221);
    -webkit-transform: skewY(1deg);
            transform: skewY(1deg);
    -webkit-transform-origin: top right;
            transform-origin: top right;
}
#quote blockquote{
    padding: 2% 3%;
    margin-left: 10%;
    margin-top: -10%;
    -webkit-transform: skewY(-1deg);
            transform: skewY(-1deg);
    -webkit-transform-origin: top right;
            transform-origin: top right;
}

#quote blockquote h2{
    font-family: 'Abril Fatface', serif;
    font-size: 28px;
    padding: 2%;
}
#quote blockquote p{
    font-weight: 200;
}

.container-arrow{
    margin-left: 63%;
    margin-top: -2%;
    padding: 2%;
}
.container-arrow .arrow{
    color: rgb(71, 70, 70);
    font-size: 15px;
    font-weight: 200;
    font-family: 'Assistant', sans-serif;
    text-transform: uppercase;
    padding: 4%;
} 
.container-arrow ion-icon{
    color: grey;
    font-weight: 400;
    -webkit-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
}

.container-arrow ion-icon{
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}

.container-arrow:hover .arrow{
    color: rgb(151, 126, 83);
    font-style: italic;
    /* font-family: 'Lato', ans-serif; */
}

.container-arrow:hover ion-icon{
    color: white;
    -webkit-transform: translate( 20px, 0);
            transform: translate( 20px, 0);
    background-color: rgb(151, 126, 83);
    border-radius: 20px;
}

.bqt-bg-blue{
    margin-top: 15%;
    height: 400px;
    background: rgb(220, 237, 242);
    -webkit-transform: skewY(-2deg);
            transform: skewY(-2deg);
    -webkit-transform-origin: top left;
            transform-origin: top left;
}

#quote #blockquote-blue{
    padding: 2% 3%;
    margin-top: -10%;
    -webkit-transform: skewY(2deg);
            transform: skewY(2deg);
    -webkit-transform-origin: top right;
            transform-origin: top right;
}

#quote #blockquote-blue h2{
    margin-top: 5%;
    font-size: 28px;
    padding: 2%;
    margin-left: 50%;
}

#quote blockquote p,
#quote #blockquote-blue p{
    color: black;
    font-size: 14px;
    padding: 3%;
}
#quote blockquote p{
    margin-right: 20%;
}

#quote #blockquote-blue p{
    margin-left: 10%;
    margin-right: 10%;
}


/* CLIENT LOGOS */
#clientes{
    width: 100%;
    height: 300px;
    margin-top: 5%;
    margin-bottom: 2%;
    background-color: white;
}
#clientes .container .row{
    margin-left:auto;
    padding: 2% 5%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}

#clientes h2{
    font-family: 'Lato', sans-serif;
    margin-left: 15%;
    padding: 2%;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 200;
}
#clientes .container .row a{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}

#clientes a .client-logo:first-of-type,
#clientes a .client-logo:nth-of-type(2),
#clientes a .client-logo:nth-of-type(3){
    width: 170px;
    padding: 5% 10%;
}

#clientes a .client-logo:nth-of-type(4),
#clientes a .client-logo:nth-of-type(5){
    width: 90px;
}


/*MEDIA QUERIES*/

@media only screen and (max-width: 800px){
    #clientes a .client-logo:first-of-type,
    #clientes a .client-logo:nth-of-type(2),
    #clientes a .client-logo:nth-of-type(3){
        width: 150px;
        padding: 5% 10%;
    }

}


@media only screen and (max-width: 768px){

    #intro .container{
        margin-top: 10%;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
    }
    #intro .intro-box{
        margin: 5% 1%;
        width: 90%;
        -webkit-box-flex:1;
            -ms-flex:1;
                flex:1;
    }
}

@media only screen and (max-width: 720px){

    #quote blockquote h2,
    #quote #blockquote-blue h2{
        font-size: 26px;
        padding: 2%;
        padding-top: 35px;
    }
    #quote blockquote p,
    #quote #blockquote-blue p{
        font-size: 14px;
    }
    #quote #blockquote-blue h2,
    #quote #blockquote-blue p{
        margin-left: -5%;
        text-align: left;
    }

    .bqt-bg{
        padding-top: 15%;
    }

    .bqt-bg-blue{
        /* margin-top: 15%; */
        height: 400px;
    }

    .container-arrow .arrow{
        font-size: 14px;
        margin-left: -15%;
    }

    #clientes a .client-logo:first-of-type,
    #clientes a .client-logo:nth-of-type(2),
    #clientes a .client-logo:nth-of-type(3){
        width: 130px;
        padding: 5% 10%;
    }

}

@media only screen and (max-width: 640px){
    
    #quote blockquote p,
    #quote #blockquote-blue p{
        font-size: 13px;
    }

    #clientes .container .row{
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-flow: row wrap;
                flex-flow: row wrap;
    }

    #clientes a .client-logo:first-of-type,
    #clientes a .client-logo:nth-of-type(2),
    #clientes a .client-logo:nth-of-type(3){
        width: 130px;
        padding: 5% 10%;
    }

}

@media only screen and (max-width: 400px){
    #intro .container-inicio .logo-gold{
        width: 150px;
    }
    
    #intro .container-inicio p{
        font-size: 13px;
        font-style: italic;
        margin-top: -4%;
        margin-left: 20%;
        width: 60%;
    }

    #intro .container{
        margin-bottom: 15%;

    }

    #quote blockquote h2,
    #quote #blockquote-blue h2{
        font-size: 24px;
        padding: 2%;
        padding-top: 35px;
    }
    #quote blockquote p,
    #quote #blockquote-blue p{
        font-size: 13px;
    }
    #quote #blockquote-blue h2,
    #quote #blockquote-blue p{
        margin-left: -5%;
    }

    .bqt-bg{
        padding-top: 15%;
    }

    .bqt-bg-blue{
        /* margin-top: 15%; */
        height: 450px;
    }

    .container-arrow .arrow{
        font-size: 13px;
        margin-left: -55%;
    }

    #clientes{
        height: auto;
    }
    #clientes .container .row{
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-flow: column nowrap;
                flex-flow: column nowrap;
    }
    
    #clientes a .client-logo:first-of-type,
    #clientes a .client-logo:nth-of-type(2),
    #clientes a .client-logo:nth-of-type(3){
        width: 170px;
        padding: 5% 5%;
        margin-left: 25%;
    }
}