/* CONTACTO */

#contacto{
    background-color: rgb(221, 220, 223);
    padding-top: 12px;
}

#contacto .container{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: auto;
    margin-top: 7%;
    margin-bottom: 12%;
}

.container-contacto {
    margin-top: -2%;
    margin-left: 3%;
    width: 33%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;
}

.container-contacto p,
.container-contacto p a{
    color: black;
    line-height: .6em;
    font-size: 14px;
}

.container-contacto p a:hover{
    text-decoration: underline;
}

.container-contacto p i{
    color: white;
    font-size: 18px;
}

.container-contacto p:nth-of-type(1),
.container-contacto p:nth-of-type(2),
.container-contacto p:nth-of-type(3){
    color: rgb(95, 94, 94);
    font-weight: 400;
    line-height: .5em;
    font-size: 13px;
}

.container-contacto p:nth-of-type(3){
    margin-bottom: 8%;
}

#contacto .container-dados{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    margin-top: -2%; 
    left:10%;
    right:10%;
    width: 66%;
}


.container-dados h4{
    width: 50%;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    font-weight: 200;
    text-transform: uppercase;
}

#contacto .container-dados form{
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
}

#contacto .container-dados form .inputs{
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin-top: 45px;
    margin-left: -50%;
}

#contacto .container-dados form input{
    padding: 3%;
    margin: 2% 0;
    width: 90%;
    border: none;
    font-weight: 200;
    font-size: 13px;
    color: rgb(105, 100, 100);
    outline: none;
}

.container-dados form textarea{
    height: 90%;
    margin-top: 42px;
    margin-bottom: 20px;
    padding: 3%;
    width: 90%;
    border: none;
    outline: none;
    font-weight: 200;
    font-size: 13px;
    color: rgb(105, 100, 100);
}

.success{
    color: rgb(18, 192, 18);
    width: 80%;
    margin: 5px 0;
    display: none;
}

.error{
    color: rgb(217, 38, 25);
    width: 80%;
    margin: 5px 0;
    display: none;
}

.message-sent,
.message-failed{
    display: block;
}


form input::-webkit-input-placeholder,
form textarea::-webkit-input-placeholder{
    margin: 0;
    color: rgb(188, 186, 186);
}


form input:-ms-input-placeholder,
form textarea:-ms-input-placeholder{
    margin: 0;
    color: rgb(188, 186, 186);
}


form input::-ms-input-placeholder,
form textarea::-ms-input-placeholder{
    margin: 0;
    color: rgb(188, 186, 186);
}


form input::placeholder,
form textarea::placeholder{
    margin: 0;
    color: rgb(188, 186, 186);
}

form button{
    display: block;
    padding: 2%;
    width: 25%;
    background-color: black;
    color: white;
    margin-top: 2%;
    font-size: 13px;
    font-weight: 200;
    outline: none;
}




/* MAP SECTION */
.container-map #map {
    width: 100%;
    height: 500px;
    margin: 0;
    padding:0;
  }


@media only screen and (max-width: 854px){
    #contacto .container{
        height: 240px;
  }

  .container-dados form textarea{
    height: 65%;
  }
}

@media only screen and (min-width: 641px and max-width 768px){
    #contacto .container{
        height: 300px;
    }
}
@media only screen and (max-width: 640px){
    #contacto .container{
        height: 440px;
    }
    .container-contacto p:nth-of-type(1),
    .container-contacto p:nth-of-type(2),
    .container-contacto p:nth-of-type(3){
        font-size: 12px;
    }

    .container-contacto p:nth-of-type(3){
        margin-bottom: 19%;
    }

    .container-contacto p:nth-of-type(4),
    .container-contacto p:nth-of-type(5),
    .container-contacto p:nth-of-type(6){
        font-size: 13px;
    }

    .container-contacto p,
    .container-contacto p a{
        font-size: 13px;
    }

    .container-contacto p i{
        font-size: 18px;
    }

    #contacto .container-dados{
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
    }

    #contacto .container-dados form{
        height: 100%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-flow: column nowrap;
                flex-flow: column nowrap;
    }

    #contacto .container-dados form .inputs{
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        margin-top: 45px;
        margin-left: 2%;
    }

    #contacto .container-dados form input,
    #contacto .container-dados form textarea{
        width: 120%;
    }

    #contacto .container-dados form textarea{
        height: 95%;
    }

    .success{
        width: 100%;
    }
}

@media only screen and (max-width: 540px){
    #contacto .container{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-flow: column nowrap;
                flex-flow: column nowrap;
        height: auto;
    }
    #contacto .container-dados{
        margin-top: 12%; 
        width: 90%;
        margin-left: 20%;
    }
    #contacto .container-contacto {
        margin-left: 30%;
        text-align: center;
    }
    .container-dados form textarea{
        height: 100%;
        margin-top: 25px;
    }
    .container-dados h4{
        margin-left: 15%;
    }
  
}

@media only screen and (max-width: 480px){

    #contacto .container-contacto{
        width: 100%;
        margin: 0;
    }

    #contacto .container-contacto:first-of-type p{
        font-size: 12px;
        line-height: 23px;
    }
    #contacto .container-dados{
        margin-top: 12%; 
        margin-left: 15%;
    }
    #contacto .container-dados h4{
        margin-left: 20%;
    }

    #contacto .container-dados form input,
    #contacto .container-dados form textarea{
        width: 70%;
    }
    #contacto .container-dados form textarea{
        margin-left: 2%;
    }
    .success,  
    .error{
        font-size: 11px;
    }

}